import { Inject, Injectable } from "@angular/core";
import { LOCAL_STORAGE, WebStorageService } from "ngx-webstorage-service";
import { Subject } from "rxjs";
import { delay, map, take } from "rxjs/operators";
import { Manga, MangaChapters } from "../Models/Manga.models";
import { MangaListService } from "./manga-list-service";
import { RSocketService } from "./rsocket-service";

@Injectable({
  providedIn: "root",
})
export class MangaService {
  public Manga: Manga;
  public chapters: MangaChapters;
  public chapterIndex: string;
  public pageIndex: number;
  public chapterSubject = new Subject<MangaChapters>();
  public updateSubject = new Subject<Manga>();
  public sendingRequest: boolean;
  search: boolean;

  constructor(
    @Inject(LOCAL_STORAGE) private storage: WebStorageService,
    private rsocketService: RSocketService,
    listService: MangaListService
  ) {
    // gets manga and chapterindex from session
    const storageManga = storage.get("Manga");
    const storageCIndex = storage.get("CIndex");
    const storagePIndex = storage.get("PIndex");

    if (storageManga) {
      this.Manga = storageManga;
      //ensure data in session isn't stale
      listService.mangaSubject.subscribe((list) => {
        const mangaFromService = list.find(
          (m) => m.realID === this.Manga.realID
        );
        this.setManga(mangaFromService);
        this.updateSubject.next(mangaFromService);
      });
    }

    if (storageCIndex) {
      this.setChapterIndex(storageCIndex);
    }

    if (storagePIndex) {
      this.setPageIndex(storagePIndex);
    }
    //Update set Manga on Change Stream
    listService.changeSubject.pipe(delay(500)).subscribe((m) => {
      const existsInList = listService.mangaList.find(
        (m) => m.realID === this.Manga.realID
      );
      if (existsInList) {
        this.setManga(m);
        this.updateSubject.next(m);
      }
    });
  }

  // sets current manga
  setManga(manga: Manga) {
    this.Manga = manga;
  }
  // sets current manga
  saveManga2Local() {
    this.storage.set("Manga", this.Manga);
  }

  setMangaForReader(manga: Manga) {
    this.Manga = manga;
    this.saveManga2Local();
  }

  setMangaFromPage(manga: Manga) {
    this.Manga = manga;
    this.chapterSubject = new Subject<MangaChapters>();
    this.getChapter(manga.realID).subscribe(this.chapterSubject);
    this.chapterSubject.subscribe((chapters) => {
      this.chapters = chapters;
      this.sendingRequest = false;
    });
  }

  setChapterIndex(index: string) {
    this.chapterIndex = index;
    this.storage.set("CIndex", this.chapterIndex);
    this.rsocketService.currentMangaSubject.next(this.Manga.t + " " + index);
  }

  setPageIndex(index: number) {
    this.pageIndex = index;
    this.storage.set("PIndex", this.pageIndex);
  }

  getManga(id): Subject<Manga> {
    const sub = new Subject<Manga>();
    if (this.rsocketService.rsocket !== undefined) {
      this.rsocketService.getMangaByID(id).subscribe(sub);
    } else {
      this.rsocketService.socketReady
        .pipe(take(1))
        .subscribe((b) => this.rsocketService.getMangaByID(id).subscribe(sub));
    }
    return sub;
  }

  getChapter(id: number) {
    const sub = new Subject<MangaChapters>();
    this.sendingRequest = true;

    if (this.rsocketService.rsocket !== undefined) {
      this.rsocketService.getChapters(id).subscribe(sub);
    } else {
      this.rsocketService.socketReady
        .pipe(take(1))
        .subscribe((b) => this.rsocketService.getChapters(id).subscribe(sub));
    }
    sub
      .pipe(map((c) => c.mangaName))
      .subscribe((n) => this.rsocketService.currentMangaSubject.next(n));
    return sub;
  }

  saveToStorage() {
    this.storage.set("Manga", this.Manga);
    this.storage.set("CIndex", this.chapterIndex);
    this.storage.set("PIndex", this.pageIndex);
  }
}
