import { Component, HostListener, OnInit } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { DeviceDetectorService } from "ngx-device-detector";
import { Manga } from "src/app/Models/Manga.models";
import { MangaListService } from "src/app/Services/manga-list-service";
import { MangaService } from "src/app/Services/manga.service";
import { RSocketService } from "src/app/Services/rsocket-service";
import { LoginComponent } from "./login/login.component";
import { RegisterComponent } from "./register/register.component";

@Component({
  selector: "app-navbar",
  templateUrl: "./navbar.component.html",
  styleUrls: ["./navbar.component.css"],
})
export class NavbarComponent implements OnInit {
  list: Manga[];
  name;
  showlist;
  tempList;
  isMobile: boolean;
  scrHeight: number;
  scrWidth: number;

  constructor(
    public dialog: MatDialog,
    public service: MangaListService,
    public rsocket: RSocketService,
    private mangaService: MangaService,
    deviceService: DeviceDetectorService
  ) {
    this.isMobile = deviceService.isMobile();
    this.getScreenSize();
  }

  ngOnInit() {
    if (this.service.mangaList) {
      this.list = this.service.mangaList.slice();
    } else {
      this.service.mangaSubject.subscribe((manga) => {
        this.list = manga.filter((item) => item.realID != -2);
      });
    }
  }

  @HostListener("window:resize", ["$event"])
  getScreenSize(event?) {
    this.scrHeight = window.innerHeight;
    this.scrWidth = window.innerWidth;
  }

  // when mouse hovers enters navbar it disables the keyboard scroll in reader
  @HostListener("mouseenter") inSearch() {
    this.mangaService.search = true;
  }

  // when mouse hovers outside of navbar it disables the list
  @HostListener("mouseleave") closeList() {
    this.showlist = false;
    this.mangaService.search = false;
  }

  @HostListener("window:beforeunload", ["$event"])
  saveToStorage(event: Event) {
    this.mangaService.saveToStorage();
  }

  // sends the chapter to the service so that manga-page can display the data
  setChapter(manga) {
    this.mangaService.setManga(manga);
    this.showlist = false;
    this.name = "";
  }

  // A temp variable takes the list to display the data being filtered
  search(n: string) {
    if (n.length > 1) {
      n = n.toUpperCase();
      this.showlist = true;
      this.tempList = this.list.sort((a, b) => a.a.localeCompare(b.a));
      this.tempList = this.tempList.filter((manga) =>
        manga.t.toUpperCase().startsWith(n)
      );
    } else {
      this.showlist = false;
    }
  }

  // handles the images
  getImage(url: string) {
    if (url.startsWith("http")) {
      return url;
    } else {
      return "/image/" + url;
    }
  }

  // dialog buttons operations
  openDialog(): void {
    this.dialog.open(RegisterComponent);
  }

  openLogin(): void {
    this.dialog.open(LoginComponent);
  }
}
