import { DeviceInfo } from "ngx-device-detector";
import { Encodable } from "rsocket-core";
import { Flowable, Single } from "rsocket-flowable";
import { Payload, Responder } from "rsocket-types";
import { Subject } from "rxjs";
import { bufferCount, map } from "rxjs/operators";
import { rxjs2Flowable } from "./RsocketUtils";

function logRequest(type: string, payload: Payload<any, Encodable>) {
  console.log(`Responder response to ${type}, data: ${payload.data || "null"}`);
}

function make(data): Payload<any, Encodable> {
  return {
    data,
    metadata: "",
  };
}

export class RSocketClientResponder implements Responder<any, Encodable> {
  mangaSubject: Subject<string>;

  constructor(mangaSubject: Subject<string>, private deviceInfo: DeviceInfo) {
    this.mangaSubject = mangaSubject;
  }

  metadataPush(payload: Payload<any, Encodable>): Single<void> {
    return Single.error(new Error("not implemented"));
  }

  fireAndForget(payload: Payload<any, Encodable>): void {
    logRequest("fire-and-forget", payload);
  }

  requestResponse(
    payload: Payload<any, Encodable>
  ): Single<Payload<any, Encodable>> {
    console.log("request-response Request from server", payload.metadata);
    return Single.of(make(this.deviceInfo));
  }

  requestStream(
    payload: Payload<any, Encodable>
  ): Flowable<Payload<any, Encodable>> {
    console.log("request-stream Request from server", payload.metadata);
    return new Flowable(
      rxjs2Flowable(
        this.mangaSubject.pipe(
          bufferCount(2),
          map((arr) => arr[arr.length - 1])
        )
      )
    );
  }

  requestChannel(
    payloads: Flowable<Payload<any, Encodable>>
  ): Flowable<Payload<any, Encodable>> {
    return Flowable.just(make("client channel response"));
  }
}
