import { Title } from "@angular/platform-browser";
import { ActivatedRoute, Router } from "@angular/router";
import { ReaderComponent } from "../Components/reader/reader.component";
import { MangaListService } from "./manga-list-service";
import { MangaService } from "./manga.service";
import { RSocketService } from "./rsocket-service";

// Not Meant to be Injected
export class ReaderService {
  constructor(
    private reader: ReaderComponent,
    protected service: MangaService,
    protected rsocketService: RSocketService,
    protected listService: MangaListService,
    protected route: ActivatedRoute,
    protected router: Router,
    protected title: Title
  ) {}

  // goes to next page
  nextPage(i) {
    this.reader.firstPage = false;

    // if it's not in vertical mode, proceed to next page by setting thier hidden status
    if (i + 1 < this.reader.display.length && !this.reader.verticalView) {
      this.reader.display[i][4] = false;
      this.reader.display[++i][4] = true;
      this.reader.currentPage = i;
      window.scroll(0, 0);
      this.service.setPageIndex(i);
    }

    // this will make the last page in vertical clickable
    else if (i + 1 >= this.reader.Index.images.length - 1) {
      this.nextChapter();
      window.scroll(0, 0);
    }

    // this will check if at end of manga
    if (
      this.service.chapterIndex ==
        this.reader.chapters.chapters[0].chapterIndex &&
      this.reader.display[this.reader.display.length - 1][0] ===
        this.reader.display[i][0]
    ) {
      this.reader.finalPage = true;
    }
  }

  // goes to previous page
  lastPage(i) {
    this.reader.finalPage = false;
    if (i != 0) {
      this.reader.display[i][4] = false;
      this.reader.display[--i][4] = true;
      this.reader.currentPage = i;
      this.service.setPageIndex(i);
    } else if (i == 0) {
      this.lastChapter();
    }

    if (
      this.reader.display.length !== 0 &&
      this.service.chapterIndex ==
        this.reader.chapters.chapters[this.reader.chapters.chapters.length - 1]
          .chapterIndex &&
      this.reader.display[0][0] === this.reader.display[i][0]
    ) {
      this.reader.firstPage = true;
    }

    window.scroll(0, 0);
  }

  // jumps to page
  jump(index) {
    this.reader.display.forEach((element) => (element[4] = false));
    this.reader.display[index][4] = true;
    this.reader.currentPage = index;
    this.service.setPageIndex(index);
    // check if first page
    if (
      this.reader.display[0][0] === this.reader.display[index][0] &&
      this.service.chapterIndex ==
        this.reader.chapters.chapters[this.reader.chapters.chapters.length - 1]
          .chapterIndex
    ) {
      this.reader.firstPage = true;
    } else {
      this.reader.firstPage = false;
    }
    // check if last page
    if (
      this.reader.display[this.reader.display.length - 1][0] ===
        this.reader.display[index][0] &&
      this.service.chapterIndex == this.reader.chapters.chapters[0].chapterIndex
    ) {
      this.reader.finalPage = true;
    } else {
      this.reader.finalPage = false;
    }
  }
  // jumps to chapter
  jumpChapter(chapter) {
    this.service.setChapterIndex("Chapter " + chapter);
    clearInterval(this.reader.interval);
    this.reader.display = [];
    this.service.setPageIndex(undefined);
    this.reader.finalPage = false;
    this.router.navigate([
      "manga-reader/",
      this.reader.list[0].realID,
      chapter,
      1,
    ]);
  }
  // goes to next chapter
  nextChapter() {
    this.reader.firstChapter = false;
    for (let i = this.reader.chapters.chapters.length - 1; i >= 0; i--) {
      if (
        this.reader.chapters.chapters[i].chapterIndex ===
          this.service.chapterIndex &&
        i - 1 >= 0
      ) {
        this.service.setChapterIndex(
          this.reader.chapters.chapters[i - 1].chapterIndex
        );
        this.reader.display = [];
        clearInterval(this.reader.interval);
        this.service.setPageIndex(undefined);

        this.router.navigate([
          "manga-reader/",
          this.reader.list[0].realID,
          parseFloat(
            this.reader.chapters.chapters[i - 1].chapterIndex.substring(8)
          ),
          1,
        ]);

        break;
      }
    }
  }

  // goes to previous chapter
  lastChapter() {
    this.reader.currentPageLoaded = false;
    this.reader.finalChapter = false;
    for (let i = this.reader.chapters.chapters.length - 1; i >= 0; i--) {
      if (
        this.reader.chapters.chapters[i].chapterIndex ===
        this.service.chapterIndex
      ) {
        this.service.setChapterIndex(
          this.reader.chapters.chapters[i + 1].chapterIndex
        );

        this.reader.display = [];
        clearInterval(this.reader.interval);

        this.service.setPageIndex(undefined);
        this.router.navigate([
          "manga-reader/",
          this.reader.list[0].realID,
          parseFloat(
            this.reader.chapters.chapters[i + 1].chapterIndex.substring(8)
          ),
          //@ts-ignore
          this.reader.chapters.chapters[i + 1].images[0][0] + 1,
        ]);
        break;
      }
    }
  }

  // gets all chapters from API
  getChapter(params) {
    // this checks if the service has the right manga
    if (
      this.service.Manga &&
      this.service.Manga.realID === parseFloat(params.get("manga"))
    ) {
      this.reader.list[0] = this.service.Manga;

      // this.reader checks whether the manga page has a pending request
      if (!this.service.sendingRequest) {
        this.service
          .getChapter(this.service.Manga.realID)
          .subscribe((chapters) => {
            this.service.chapters = chapters;
            this.service.sendingRequest = false;

            this.reader.setup(params);
            this.title.setTitle(
              this.reader.list[0].t + ": Chapter " + this.reader.chapterIndex
            );
          });
      }

      // else wait for request
      else {
        this.service.chapterSubject.subscribe((chapters) => {
          this.service.chapters = chapters;

          this.reader.setup(params);
          this.title.setTitle(
            this.reader.list[0].t + ": Chapter " + this.reader.chapterIndex
          );
          this.service.sendingRequest = false;
        });
      }
    }

    // else wrong manga
    else {
      this.rsocketService
        .getMangaByID(params.get("manga"))
        .subscribe((manga) => {
          this.reader.list[0] = manga;
          this.service.setManga(manga);

          this.service
            .getChapter(this.service.Manga.realID)
            .subscribe((chapters) => {
              this.service.chapters = chapters;
              this.service.sendingRequest = false;

              this.reader.setup(params);
              this.title.setTitle(
                this.reader.list[0].t + ": Chapter " + this.reader.chapterIndex
              );
            });
        });
    }
  }

  // sets the current chapter
  setChapter(params) {
    const filtered = this.reader.chapters.chapters.filter(
      (chapter) => chapter.chapterIndex === "Chapter " + params.get("index")
    );

    this.reader.Index = filtered[0];
    this.service.setChapterIndex("Chapter " + params.get("index"));
    this.populateDisplay();

    // check if first page
    if (
      this.service.chapterIndex ==
      this.reader.chapters.chapters[this.reader.chapters.chapters.length - 1]
        .chapterIndex
    ) {
      this.reader.firstChapter = true;
      this.reader.firstPage = true;
    } else {
      this.reader.firstChapter = false;
      this.reader.firstPage = false;
    }

    // check if final chapter
    if (
      this.service.chapterIndex == this.reader.chapters.chapters[0].chapterIndex
    ) {
      this.reader.finalChapter = true;
    } else {
      this.reader.finalChapter = false;
    }
  }

  // uses interval to add pages to the display
  populateDisplay() {
    let i = 0;
    const array = this.reader.Index.images.slice().reverse();
    this.reader.interval = setInterval(() => {
      if (i === array.length) {
        return;
      }

      this.reader.display.push(array[i]);
      this.reader.display[i][5] = false;

      if (i === this.reader.currentPage) {
        this.reader.display[i][4] = true;
      } else {
        this.reader.display[i][4] = false;
      }

      i++;
    }, 0);
  }
}
