import {
  Component,
  ElementRef,
  HostListener,
  OnDestroy,
  OnInit,
} from "@angular/core";
import { Title } from "@angular/platform-browser";
import { ActivatedRoute, ParamMap, Router } from "@angular/router";
import { DeviceDetectorService } from "ngx-device-detector";
import { Chapters, MangaChapters } from "../../Models/Manga.models";
import { MangaListService } from "../../Services/manga-list-service";
import { MangaService } from "../../Services/manga.service";
import { ReaderService } from "../../Services/reader-service";
import { RSocketService } from "../../Services/rsocket-service";
// tslint:disable: triple-equals
// tslint:disable: align
// tslint:disable: one-line
// tslint:disable: no-trailing-whitespace
// tslint:disable: quotemark
// tslint:disable: max-line-length
// tslint:disable: radix

@Component({
  selector: "app-reader",
  templateUrl: "./reader.component.html",
  styleUrls: ["./reader.component.css"],
})
export class ReaderComponent implements OnInit, OnDestroy {
  list = [];

  readerService: ReaderService;

  constructor(
    protected service: MangaService,
    protected listService: MangaListService,
    protected route: ActivatedRoute,
    private element: ElementRef,
    protected router: Router,
    protected title: Title,
    rsocketService: RSocketService,
    deviceService: DeviceDetectorService
  ) {
    this.isMobile = deviceService.isMobile();
    this.darkMode();

    // If on phone, the pages will be displayed vertically
    if (this.isMobile) {
      this.verticalView = !this.verticalView;
    }
    this.readerService = new ReaderService(
      this,
      service,
      rsocketService,
      listService,
      route,
      router,
      title
    );
  }

  imageSize = 60;
  isMobile: boolean;
  verticalView = false;
  dark = false;
  chapters: MangaChapters;
  Index: Chapters;
  chapterIndex: string;
  interval;
  display = [];
  currentPage = 0;
  firstPage;
  firstChapter = false;
  finalPage = false;
  finalChapter = false;
  loadedImage = false;
  currentPageLoaded = false;
  expandImage = false;

  changeView() {
    this.verticalView = !this.verticalView;
  }

  resizeImages(size: number) {
    this.imageSize = size;
    this.expandImage = !this.expandImage;
  }

  darkMode() {
    this.dark = !this.dark;

    if (this.dark) {
      this.element.nativeElement.ownerDocument.body.style.backgroundColor =
        "black";
    } else {
      this.element.nativeElement.ownerDocument.body.style.backgroundColor =
        "white";
    }
  }

  disableSpinner() {
    this.currentPageLoaded = true;
    return null;
  }

  ngOnInit() {
    this.route.paramMap.subscribe((params) => {
      this.display = [];
      clearInterval(this.interval);

      // tslint:disable: radix
      if (
        isNaN(parseInt(params.get("manga"))) ||
        isNaN(parseInt(params.get("index")))
      ) {
        this.router.navigate(["**"]);
      }

      // if manga exists in session
      if (
        this.service.Manga &&
        parseFloat(params.get("manga")) === this.service.Manga.realID
      ) {
        this.list[0] = this.service.Manga;
        this.setup(params);
        this.title.setTitle(this.list[0].t + ": Chapter " + this.chapterIndex);
      }

      // if the mangalist is loaded
      else if (this.listService.mangaList) {
        this.list = this.listService.mangaList.filter(
          (item) => item.realID == parseInt(params.get("manga"))
        );

        this.setup(params);
        this.title.setTitle(this.list[0].t + ": Chapter " + this.chapterIndex);
      }

      // check if this is part of the latest manga
      else if (!this.listService.mangaList && this.checkLatest(params)) {
        this.list = this.listService.recent
          .slice()
          .filter((item) => item.realID == parseInt(params.get("manga")));

        this.setup(params);
        this.title.setTitle(this.list[0].t + ": Chapter " + this.chapterIndex);
      } else {
        // this will run if it's part of the latest manga
        this.listService.recentSubject.subscribe((latest) => {
          if (!this.list) {
            this.list = latest.filter(
              (item) => item.realID == parseInt(params.get("manga"))
            );

            if (this.list.length != 0) {
              this.setup(params);
              this.title.setTitle(
                this.list[0].t + ": Chapter " + this.chapterIndex
              );
            }
          }
        });

        // this will run in all other cases
        this.listService.mangaSubject.subscribe((manga) => {
          if (this.list.length == 0) {
            this.list = manga.filter(
              (item) => item.realID == parseInt(params.get("manga"))
            );
            this.setup(params);
            this.title.setTitle(
              this.list[0].t + ": Chapter " + this.chapterIndex
            );
          }
        });

        // this will load the manga's info straight from the database if the above two functions fail
        this.loadFromDatabase(params);
      }
    });
  }

  // setup variables based on the route
  setup(params) {
    //saves manga 2 local storage for later reading
    this.service.saveManga2Local();
    this.chapterIndex = params.get("index");

    // if chapterlist is in the service
    if (
      this.service.chapters &&
      this.list[0].t === this.service.chapters.mangaName
    ) {
      this.chapters = this.service.chapters;
      this.readerService.setChapter(params);
    } else {
      this.service.setManga(this.list[0]);
      this.readerService.getChapter(params);
    }

    // sets page
    if (this.service.pageIndex != undefined) {
      this.currentPage = this.service.pageIndex;
    } else if (parseInt(params.get("page")) > 0) {
      this.currentPage = parseInt(params.get("page")) - 1;
    } else {
      this.currentPage = 0;
    }
  }

  // checks whether manga is part of the latest manga
  checkLatest(params) {
    try {
      return (
        params.get("manga") ===
        this.listService.recent
          .find((manga) => manga.realID.toString() === params.get("manga"))
          .realID.toString()
      );
    } catch (error) {
      return false;
    }
  }

  // this will load the manga from the database if the site doesn't currently have it
  loadFromDatabase(params: ParamMap) {
    setTimeout(() => {
      // this checks if the service has a manga stored
      if (this.list.length === 0) {
        this.readerService.getChapter(params);
      }
    }, 3000);
  }

  @HostListener("window:keydown", ["$event"])
  keyEvent(event: KeyboardEvent) {
    if (this.service.search) {
      //won't do anything because we're in the searchbar
    } else if (
      (event.keyCode === 39 || event.keyCode === 32 || event.keyCode === 68) &&
      !this.verticalView
    ) {
      this.readerService.nextPage(this.currentPage);
      event.preventDefault();
    } else if (
      (event.keyCode === 37 || event.keyCode === 65) &&
      !this.verticalView
    ) {
      this.readerService.lastPage(this.currentPage);
      event.preventDefault();
    }
  }

  ngOnDestroy() {
    //reset dark theme
    this.element.nativeElement.ownerDocument.body.style.backgroundColor =
      "white";
    //clear pageindex from session
    this.service.setPageIndex(undefined);
  }
}
