import {
  APPLICATION_JSON,
  BufferEncoders,
  ClientConfig,
  Encodable,
  encodeBearerAuthMetadata,
  encodeCompositeMetadata,
  MESSAGE_RSOCKET_AUTHENTICATION,
  MESSAGE_RSOCKET_COMPOSITE_METADATA,
} from "rsocket-core";
import RSocketWebSocketClient from "rsocket-websocket-client";
import { JSONBufferSerializers } from "./JSONBufferSerializer";
import { RSocketClientResponder } from "./RSocketClientResponder";

export class RSocketConfig {
  rsocketConfig: ClientConfig<any, Encodable>;

  constructor(id, jwt: string, responder: RSocketClientResponder) {
    // const rsocketUrl = "ws://localhost:8080/rsocket";
    const rsocketUrl = "wss://manga-spring.herokuapp.com/rsocket";

    const transport = new RSocketWebSocketClient(
      {
        url: rsocketUrl,
      },
      BufferEncoders
    );

    const connectionConfig = {
      keepAlive: 20000,
      lifetime: 180000,
      dataMimeType: APPLICATION_JSON.string,
      metadataMimeType: MESSAGE_RSOCKET_COMPOSITE_METADATA.string,
      //setup payload
      payload: {
        data: id,
        metadata: encodeCompositeMetadata([
          [MESSAGE_RSOCKET_AUTHENTICATION, encodeBearerAuthMetadata(jwt)],
        ]),
      },
    };

    this.rsocketConfig = {
      serializers: JSONBufferSerializers,
      setup: connectionConfig,
      responder: responder,
      transport: transport,
    };
  }
}
