import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { DeviceDetectorService } from "ngx-device-detector";
import { LOCAL_STORAGE, WebStorageService } from "ngx-webstorage-service";
import { ClientConfig, Encodable, RSocketClient } from "rsocket-core";
import { ReactiveSocket } from "rsocket-types";
import { Observable, Subject } from "rxjs";
import { delay, retryWhen, take, toArray } from "rxjs/operators";
import { Manga, MangaChapters } from "../Models/Manga.models";
import { RSocketClientResponder } from "../RSocketUtil/RSocketClientResponder";
import { RSocketConfig } from "../RSocketUtil/RSocketConfig";
import {
  extractPayloadData,
  flowableSignal2RXJS,
  routedPayload,
  singleSignal2RXJS,
} from "../RSocketUtil/RsocketUtils";

@Injectable({
  providedIn: "root",
})
export class RSocketService {
  client: RSocketClient<any, Encodable>;
  rsocket: ReactiveSocket<any, Encodable>;
  socketReady = new Subject<ReactiveSocket<any, Encodable>>();
  changeStream = new Subject<Manga>();
  mangaListSubject = new Subject<Manga>();
  latestSubject = new Subject<Manga[]>();
  popularSubject = new Subject<Manga[]>();
  connectFailed = false;
  connected = false;
  currentMangaSubject = new Subject<string>();

  constructor(
    http: HttpClient,
    deviceService: DeviceDetectorService,
    @Inject(LOCAL_STORAGE) storage: WebStorageService
  ) {
    let id = storage.get("uniqueId");
    if (!id) {
      id =
        deviceService.os +
        "-Manga-Spring-" +
        Math.floor(Math.random() * Math.floor(1000));
      storage.set("uniqueId", id);
    }
    const responder = new RSocketClientResponder(
      this.currentMangaSubject,
      deviceService.getDeviceInfo()
    );

    const tokenRequest = http
      .get("/api/getToken", {
        headers: new HttpHeaders().set("Content-Type", "text/plain"),
        responseType: "text",
      })
      .pipe(retryWhen((errors) => errors.pipe(delay(1000), take(10))));

    const tokenConsumer = (token: string) => {
      this.connectRSocket(
        new RSocketConfig(id, token, responder).rsocketConfig
      );
      this.subscribe2RSocket();
    };

    tokenRequest.subscribe(tokenConsumer);

    setInterval(() => {
      if (this.connectFailed) {
        this.connectFailed = false;
        console.log("Attempting Reconnect");
        tokenRequest.subscribe(tokenConsumer);
      }
    }, 3000);
  }

  connectRSocket(rsocketConfig: ClientConfig<any, Encodable>) {
    new RSocketClient(rsocketConfig).connect().subscribe({
      onComplete: (s) => this.publishConnectEvent(s),
      onError: (err) => {
        console.log("Failed to Connect to RSocket", err);
        this.connectFailed = true;
        this.connected = false;
      },
    });
  }

  subscribe2RSocket() {
    this.socketReady.subscribe((rsocket) => {
      if (!this.connected) {
        this.connected = true;
        console.log(" RSocket Connection is: CONNECTED ");
        this.getManga(rsocket).subscribe(this.mangaListSubject);
        this.getPopular(rsocket).subscribe(this.popularSubject);
        this.getLatest(rsocket).subscribe(this.latestSubject);
        this.watchDBChanges(rsocket).subscribe(this.changeStream);
      }
    });
  }

  //get All manga
  getManga(socket: ReactiveSocket<any, Encodable>): Observable<Manga> {
    return new Observable<Manga>((subscriber) => {
      socket
        .requestStream(routedPayload("get-mangas"))
        .map(extractPayloadData)
        .subscribe(flowableSignal2RXJS(subscriber));
    });
  }

  getMangaByID(id: number): Observable<Manga> {
    return new Observable<Manga>((subscriber) => {
      this.rsocket
        .requestResponse(routedPayload("get-manga-" + id))
        .map(extractPayloadData)
        .subscribe(singleSignal2RXJS(subscriber));
    });
  }

  getPopular(socket: ReactiveSocket<any, Encodable>): Observable<Manga[]> {
    return new Observable<Manga>((subscriber) => {
      socket
        .requestStream(routedPayload("popular-manga"))
        .map(extractPayloadData)
        .subscribe(flowableSignal2RXJS(subscriber, 5));
    }).pipe(toArray());
  }

  getLatest(socket: ReactiveSocket<any, Encodable>): Observable<Manga[]> {
    return new Observable<Manga>((subscriber) => {
      socket
        .requestStream(routedPayload("latest-manga"))
        .map(extractPayloadData)
        .subscribe(flowableSignal2RXJS(subscriber));
    }).pipe(toArray());
  }

  getChapters(id: number): Observable<MangaChapters> {
    return new Observable<MangaChapters>((subscriber) => {
      this.rsocket
        .requestResponse(routedPayload("get-chapters-" + id))
        .map(extractPayloadData)
        .subscribe(singleSignal2RXJS(subscriber));
    });
  }

  watchDBChanges(socket: ReactiveSocket<any, Encodable>): Observable<Manga> {
    return new Observable<Manga>((subscriber) => {
      socket
        .requestStream(routedPayload("mongo-change-stream"))
        .map(extractPayloadData)
        .subscribe(flowableSignal2RXJS(subscriber));
    });
  }

  publishConnectEvent(sock: ReactiveSocket<any, Encodable>) {
    sock.connectionStatus().subscribe((status) => {
      if (status.kind !== "CONNECTED") {
        console.log("RSocket Connection is: " + status.kind);
        this.connected = false;
        this.rsocket = undefined;
        if (status.kind === "ERROR") {
          this.connectFailed = true;
        }
      } else {
        this.rsocket = sock;
        this.socketReady.next(sock);
      }
    });
  }
}
