import { Component, Inject, OnInit } from "@angular/core";
import { Title } from "@angular/platform-browser";
import { DeviceDetectorService } from "ngx-device-detector";
import { LOCAL_STORAGE, WebStorageService } from "ngx-webstorage-service";
import { Manga } from "../../Models/Manga.models";
import { MangaListService } from "../../Services/manga-list-service";
import { MangaService } from "../../Services/manga.service";

@Component({
  selector: "app-home",
  templateUrl: "./home.component.html",
  styleUrls: ["./home.component.css"],
})
export class HomeComponent implements OnInit {
  portrait: boolean;
  isMobile: boolean;
  latestManga: Manga[] = [];
  showGenre = false;
  // tslint:disable-next-line:max-line-length
  anouncement =
    "Welcome to Manga-Spring! This site was built to showcase technical expertise in full-stack development. The coolest thing about this site is that it uses the RSocket network protocol to send/recieve data";
  genres = [
    "Action",
    "Adventure",
    "Comedy",
    "Comic",
    "Drama",
    "Demons",
    "Doujinshi",
    "Ecchi",
    "Fantasy",
    "Harem",
    "Historical",
    "Horror",
    "Josei",
    "Martial Arts",
    "Magic",
    "Mature",
    "Mecha",
    "Mystery",
    "Psychological",
    "Romance",
    "Seinen",
    "School Life",
    "Shoujo",
    "Sci-fi",
    "Shounen",
    "Supernatural",
    "Sports",
    "Tragedy",
  ];

  currentlyReading: Manga;
  currentlyReadingChapter: string;
  routerLinkText: string;

  constructor(
    private listService: MangaListService,
    public mangaService: MangaService,
    title: Title,
    deviceService: DeviceDetectorService,
    @Inject(LOCAL_STORAGE) storage: WebStorageService
  ) {
    title.setTitle("MangaSpring");
    this.isMobile = deviceService.isMobile();
    this.currentlyReading = storage.get("Manga");
    this.currentlyReadingChapter = mangaService.chapterIndex;
  }

  getLatest() {
    // this.latestManga = this.latestManga.filter(item => item.ld > new Date(Date.now()).getTime() / 1000 - 604800 && !item.c.includes("Adult"));
    this.sortByTime();
    //set size later
    this.latestManga.length = 6;
  }

  show() {
    this.showGenre = !this.showGenre;
  }

  setChapter(manga, chapter) {
    this.mangaService.setManga(manga);
    this.mangaService.setChapterIndex(chapter);
  }

  sortByTime() {
    return this.latestManga.sort((a, b) => b.ld - a.ld);
  }

  getImage(url: string) {
    if (url.startsWith("http")) {
      return url;
    } else {
      return "/image/" + url;
    }
  }

  updateList(manga: Manga) {
    if (!this.latestManga.find((m) => m.realID === manga.realID))
      this.latestManga.push(manga);
    else {
      for (let i = 0; i < this.latestManga.length; i++) {
        if (this.latestManga[i].realID === manga.realID)
          this.latestManga[i] = manga;
      }
    }
    this.getLatest();
  }

  ngOnInit() {
    //fix when we get a lot of manga
    if (this.listService.mangaList) {
      this.latestManga = this.listService.mangaList.slice();
      this.getLatest();
      this.listService.recentSubject.subscribe((list) => {
        this.latestManga = list;
        this.getLatest();
        this.listService.recent = list;
      });
    } else if (!this.listService.mangaList && this.listService.recent) {
      this.latestManga = this.listService.recent;
      this.getLatest();
    } else {
      this.listService.mangaSubject.subscribe((list) => {
        this.latestManga = list.slice();
        this.getLatest();
      });
    }
    this.listService.changeSubject.subscribe((m) => this.updateList(m));
    window.scroll(0, 0);
  }
}
