import { Injectable } from "@angular/core";
import { Subject } from "rxjs";
import { toArray } from "rxjs/operators";
import { Manga } from "../Models/Manga.models";
import { RSocketService } from "./rsocket-service";

@Injectable({
  providedIn: "root",
})
export class MangaListService {
  public mangaList: Manga[];
  popular: Manga[];
  recent: Manga[];
  postlist: Manga[];
  IsLoaded = false;
  sendingRequest: boolean;

  // subjects allow for multicasted observables
  mangaSubject = new Subject<Manga[]>();
  recentSubject = new Subject<Manga[]>();
  changeSubject = new Subject<Manga>();

  constructor(rsocketService: RSocketService) {
    //multicast manga values to the rest of application
    rsocketService.mangaListSubject
      .pipe(toArray())
      .subscribe(this.mangaSubject);
    rsocketService.latestSubject.subscribe(this.recentSubject);
    rsocketService.popularSubject.subscribe((p) => (this.popular = p));
    this.recentSubject.subscribe((r) => (this.recent = r));
    this.mangaSubject.subscribe((list) => {
      this.mangaList = list;
      this.IsLoaded = true;
      //subscribe to rsocket change stream for real-time updates
      rsocketService.changeStream.subscribe(this.changeSubject);
      this.changeSubject.subscribe((m) => this.changeStreamEventHandler(m));
    });
  }
  //Handle Changed manga
  changeStreamEventHandler(manga: Manga): void {
    console.log(manga.t + " Was Updated");
    this.updateArray(this.mangaList, manga);
    this.updateArray(this.popular, manga);
    this.updateArray(this.recent, manga);
  }

  updateArray(arr: any[], manga: Manga) {
    const exists = !arr.find((m) => m.realID === manga.realID);
    //if manga was just inserted into table
    if (exists && arr != this.popular) {
      arr.push(manga);
    }
    //else find manga in array and replace
    else {
      for (let i = 0; i < arr.length; i++) {
        if (arr[i].realID === manga.realID) arr[i] = manga;
      }
    }
  }
}
