import { Component, OnInit } from "@angular/core";
import { Title } from "@angular/platform-browser";
import { ActivatedRoute, Router } from "@angular/router";
import { DeviceDetectorService } from "ngx-device-detector";
import { Manga } from "src/app/Models/Manga.models";
import { MangaListService } from "src/app/Services/manga-list-service";
import { MangaService } from "src/app/Services/manga.service";

@Component({
  selector: "app-manga-page",
  templateUrl: "./manga-page.component.html",
  styleUrls: ["./manga-page.component.css"],
})
export class MangaPageComponent implements OnInit {
  manga: Manga;
  list = [];
  relatedManga: Manga[] = [];
  genres = [];
  chapters: any[];
  location = [];
  showChapter = false;
  showSpinner = true;
  isMobile: boolean;

  constructor(
    public service: MangaService,
    private mangaListService: MangaListService,
    private title: Title,
    private route: ActivatedRoute,
    private router: Router,
    deviceService: DeviceDetectorService
  ) {
    window.scroll(0, 0);
    this.isMobile = deviceService.isMobile();
  }

  fixString() {
    this.manga.info.description = this.manga.info.description
      .split("&#039;")
      .join("'");
    this.manga.info.description = this.manga.info.description
      .split("&quot;")
      .join("'");
    this.manga.info.description = this.manga.info.description
      .split("&amp;")
      .join("'");
    this.manga.info.description = this.manga.info.description
      .split("&nbsp;")
      .join("'");
  }

  showMore() {
    this.showChapter = !this.showChapter;
  }

  startReading() {
    this.service.setPageIndex(undefined);
    this.router.navigate([
      "manga-reader/",
      this.manga.realID,
      this.manga.info.chapters[this.manga.info.chapters.length - 1][0],
      1,
    ]);
  }

  // renders a list of 5 mangas that are related to the current genre
  relatedMangas() {
    const filter = (m) => {
      let i = 0;
      this.genres.forEach((g) => {
        if (m.c.includes(g)) i++;
      });
      return i >= 2;
    };

    if (this.mangaListService.mangaList) {
      this.relatedManga = this.mangaListService.mangaList
        .slice()
        .filter((manga) => this.manga.realID !== manga.realID);
      this.shuffle(this.relatedManga);
      // Until Our Numbers are up, this logic must be disabled
      this.relatedManga = this.relatedManga.filter(filter);
      if (this.relatedManga.length > 3) this.relatedManga.length = 3;
    } else {
      this.mangaListService.mangaSubject.subscribe((l) => {
        this.relatedManga = l
          .slice()
          .filter((manga) => this.manga.realID !== manga.realID);
        this.shuffle(this.relatedManga);
        // Until Our Numbers are up, this logic must be disabled
        this.relatedManga = this.relatedManga.filter(filter);
        if (this.relatedManga.length > 3) this.relatedManga.length = 3;
      });
    }
  }

  // gets image from api
  getImage(url: string) {
    if (url.startsWith("http")) {
      return url;
    } else {
      return "/image/" + url;
    }
  }

  // filter function that handles the manga
  filter(params) {
    // tslint:disable-next-line:radix
    this.list = this.list.filter(
      (manga) => manga.realID === parseInt(params.get("realID"))
    );

    if (this.list[0] == null || isNaN(parseInt(params.get("realID")))) {
      this.router.navigate(["**"]);
    }

    this.title.setTitle(this.list[0].t);
    this.manga = this.list[0];
    this.fixString();
    this.service.setManga(this.list[0]);
    this.genres = this.manga.c;
    this.manga.info.numOfChapters = this.manga.info.chapters.length;
    this.chapters = this.manga.info.chapters;
  }

  // Pushes every 50 element into an array
  checkIndex(i) {
    i = parseFloat(i);
    if (i % 50 === 0 && i !== 0) {
      if (this.location.find((num) => num === i)) {
        return;
      } else {
        this.location.push(parseFloat(i));
      }
    }
  }

  jumpToChapter(chapter) {
    if (chapter - 50 > 1) {
      const x = document.getElementById("chapter " + (chapter - 50));
      x.scrollIntoView();
    } else {
      const x = document.getElementById(
        "chapter " + this.chapters[this.chapters.length - 1][0]
      );
      x.scrollIntoView();
    }
  }

  jumpDisplay(item) {
    if (item - 50 > 1) {
      return item - 50;
    } else {
      return this.chapters[this.chapters.length - 1][0];
    }
  }

  // shuffles an array
  shuffle(array) {
    // tslint:disable-next-line:one-variable-per-declaration
    let currentIndex = array.length,
      temporaryValue,
      randomIndex;

    // While there remain elements to shuffle...
    while (0 !== currentIndex) {
      // Pick a remaining element...
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex -= 1;

      // And swap it with the current element.
      temporaryValue = array[currentIndex];
      array[currentIndex] = array[randomIndex];
      array[randomIndex] = temporaryValue;
    }

    return array;
  }

  IsLatest(params) {
    try {
      return (
        params.get("realID") ===
        this.mangaListService.recent
          .find((manga) => manga.realID.toString() === params.get("realID"))
          .realID.toString()
      );
    } catch (error) {
      return false;
    }
  }

  IsPopular(params) {
    try {
      return (
        params.get("realID") ===
        this.mangaListService.popular
          .find((manga) => manga.realID.toString() === params.get("realID"))
          .realID.toString()
      );
    } catch (error) {
      return false;
    }
  }

  loadFromDatabase(params) {
    setTimeout(() => {
      if (this.list.length === 0) {
        if (
          this.service.Manga &&
          this.service.Manga.realID === parseFloat(params.get("realID"))
        ) {
          this.list[0] = this.service.Manga;
          this.filter(params);
          this.relatedMangas();
        } else {
          this.service.getManga(params.get("realID")).subscribe((manga) => {
            this.list[0] = manga;
            this.service.setMangaFromPage(manga);

            this.filter(params);
            this.relatedMangas();
          });
        }
      }
    }, 1000);
  }

  resetSpinner() {
    this.list.forEach((manga) => (manga.info.imageLoaded = false));
  }

  ngOnInit() {
    this.route.paramMap.subscribe((params) => {
      this.resetSpinner();
      window.scroll(0, 0);

      // if manga is in manga service
      if (
        this.service.Manga &&
        parseFloat(params.get("realID")) === this.service.Manga.realID
      ) {
        this.list[0] = this.service.Manga;

        this.filter(params);
        this.relatedMangas();
        // if any manga are in the api service
      } else if (this.mangaListService.mangaList) {
        this.list = this.mangaListService.mangaList.slice();
        this.filter(params);
        this.service.setMangaFromPage(this.list[0]);
        this.relatedMangas();

        // if in popular manga
      } else if (!this.mangaListService.mangaList && this.IsPopular(params)) {
        this.list = this.mangaListService.popular.slice();
        this.filter(params);
        this.relatedMangas();

        // if in latest manga
      } else if (!this.mangaListService.mangaList && this.IsLatest(params)) {
        this.list = this.mangaListService.recent.slice();
        this.filter(params);
        this.service.setMangaFromPage(this.list[0]);
        this.relatedMangas();
      } else {
        // this will load the manga's info straight from the database if the next functions fail
        this.loadFromDatabase(params);

        // this will run if it's part of the latest manga
        this.mangaListService.recentSubject.subscribe((latest) => {
          if (!this.list) {
            this.list = latest.filter(
              (item) => item.realID == parseInt(params.get("realID"))
            );

            if (this.list.length != 0) {
              this.filter(params);
              this.service.setMangaFromPage(this.list[0]);
              this.relatedMangas();
            }
          }
        });

        this.mangaListService.mangaSubject.subscribe((manga) => {
          if (!this.list) {
            this.list = manga.slice();
            this.filter(params);
            this.service.setMangaFromPage(this.list[0]);
            this.relatedMangas();
          }
        });
      }
      this.service.updateSubject.subscribe((m) => {
        if (this.manga.realID === m.realID) {
          this.manga = m;
          this.list[0] = m;
          this.filter(params);
        }
      });
    });
  }
}
