import {
  Encodable,
  encodeCompositeMetadata,
  encodeRoute,
  MESSAGE_RSOCKET_ROUTING,
} from "rsocket-core";
import { IFutureSubscriber } from "rsocket-flowable/Single";
import { ISubscriber, Payload } from "rsocket-types";
import { Observable, Subscriber } from "rxjs";
import { take } from "rxjs/operators";

export function routedPayload(
  route: string,
  data: any = null
): Payload<any, Encodable> {
  return {
    data: data,
    metadata: encodeCompositeMetadata([
      [MESSAGE_RSOCKET_ROUTING, encodeRoute(route)],
    ]),
  };
}

export function basicPayload(data): Payload<any, Encodable> {
  return {
    data: data,
    metadata: null,
  };
}

export const extractPayloadData = (p) => p.data;

export function singleSignal2RXJS(
  subscriber: Subscriber<any>
): Partial<IFutureSubscriber<any>> {
  return {
    onComplete: (n) => {
      subscriber.next(n);
      subscriber.complete();
    },
    onError: (e) => subscriber.error(e),
  };
}

export function flowableSignal2RXJS(
  subscriber: Subscriber<any>,
  requestN: number = Number.MAX_SAFE_INTEGER
): Partial<ISubscriber<any>> {
  return {
    onNext: (n) => subscriber.next(n),
    onComplete: () => subscriber.complete(),
    onError: (e) => subscriber.error(e),
    onSubscribe: (s) => s.request(requestN),
  };
}

export function rxjs2Flowable(observabl: Observable<any>) {
  return (subscriber: ISubscriber<any>) => {
    subscriber.onSubscribe({
      request: (n) => {
        observabl
          .pipe(take(n))
          .subscribe((s) => subscriber.onNext(basicPayload(s)));
      },
      cancel: () => console.log("Cancelled Server Request"),
    });
  };
}
