import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { HomeComponent } from "./Components/home/home.component";
import { MangaListComponent } from "./Components/manga-list/manga-list.component";
import { MangaPageComponent } from "./Components/manga-page/manga-page.component";
import { ReaderComponent } from "./Components/reader/reader.component";

const routes: Routes = [
  { path: "", redirectTo: "/home", pathMatch: "full" },
  {
    path: "manga-reader/:manga/:index",
    redirectTo: "/manga-reader/:manga/:index/1",
    pathMatch: "full",
  },
  {
    path: "manga-reader/:manga",
    redirectTo: "/manga-page/:manga",
    pathMatch: "full",
  },
  { path: "home", component: HomeComponent },
  { path: "manga-list", component: MangaListComponent },
  { path: "manga-list/:genre", component: MangaListComponent },
  { path: "manga-page/:realID", component: MangaPageComponent },
  { path: "manga-reader/:manga/:index/:page", component: ReaderComponent },
  //{path: '**', component: PageNotFoundComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
