import { Component, HostListener, OnInit } from "@angular/core";
import { Manga } from "src/app/Models/Manga.models";
import { MangaListService } from "src/app/Services/manga-list-service";
import { RSocketService } from "src/app/Services/rsocket-service";

@Component({
  selector: "app-popular-manga",
  templateUrl: "./popular-manga.component.html",
  styleUrls: ["./popular-manga.component.css"],
})
export class PopularMangaComponent implements OnInit {
  favorites: Manga[];
  filter: Manga[];
  wierdSize: boolean;

  constructor(
    private service: MangaListService,
    private rsocketservice: RSocketService
  ) {
    this.getScreenSize();
  }

  getImage(url: string) {
    if (url.startsWith("http")) {
      return url;
    } else {
      return "/image/" + url;
    }
  }

  ngOnInit() {
    if (this.service.popular) {
      this.favorites = this.service.popular;
    } else {
      this.rsocketservice.popularSubject.subscribe((apiResponse) => {
        this.favorites = apiResponse;
        this.service.popular = apiResponse;
      });
    }
  }

  @HostListener("window:resize", ["$event"])
  getScreenSize(event?) {
    if (window.innerWidth > 767 && window.innerWidth < 993)
      this.wierdSize = true;
    else this.wierdSize = false;
  }
}
