import { Directive, HostBinding, Input } from "@angular/core";
@Directive({
  selector: "img",
  host: {
    "(error)": "updateUrl()",
    "(load)": "load()",
    "[src]": "src",
  },
})
export class ImagePreloadDirective {
  @Input() src: string;
  @HostBinding("class") className;

  updateUrl() {
    this.src = "../../../assets/noimage.jpeg";
  }
  load() {
    this.className = "image-loaded";
  }
}
